body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 200s linear;
          animation: App-logo-spin infinite 200s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.line {
  position: relative;
  width: 0px;
  margin: auto;
  border-right: 2px solid rgba(255, 255, 255, 0.75);
  font-size: 180%;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;

}
/*Animação*/
.anim-typewriter {
  -webkit-animation: typewriter 8s steps(9) 500ms infinite forwards,
  blinkTextCursor 500ms steps(15) infinite normal;
          animation: typewriter 8s steps(9) 500ms infinite forwards,
  blinkTextCursor 500ms steps(15) infinite normal;
   
  /* animation-iteration-count: 3; */
}
@-webkit-keyframes typewriter {
  0% {
     width: 0px;
  }
  5% {
    width: 130px;
  }
  10% {
        width: 125px;
  }
  15% {
    width: 125px;
  }
  20% {
    width: 125px;
  }
  25% {
     width: 125px;
  }
  30% {
    width: 125px;
  }
  75% {
        width: 125px;
  }
  80% {
    width: 125px;
    -webkit-animation-duration: 10s;
            animation-duration: 10s
  }
  90% {
        width: 0px;
  }
  95% {
    width: 0px;
}
  100% {
        width: 0px;
  }
}
@keyframes typewriter {
  0% {
     width: 0px;
  }
  5% {
    width: 130px;
  }
  10% {
        width: 125px;
  }
  15% {
    width: 125px;
  }
  20% {
    width: 125px;
  }
  25% {
     width: 125px;
  }
  30% {
    width: 125px;
  }
  75% {
        width: 125px;
  }
  80% {
    width: 125px;
    -webkit-animation-duration: 10s;
            animation-duration: 10s
  }
  90% {
        width: 0px;
  }
  95% {
    width: 0px;
}
  100% {
        width: 0px;
  }
}
@-webkit-keyframes blinkTextCursor {
  from {
        border-right-color: rgba(255, 255, 255, 0.75);
  }
  to {
        border-right-color: transparent;
  }
}
@keyframes blinkTextCursor {
  from {
        border-right-color: rgba(255, 255, 255, 0.75);
  }
  to {
        border-right-color: transparent;
  }
}
